import sIndexList from "../../component/index/s-index-list.vue"
export default {
	data() {
		return {
			listData: []
		}
	},
	components: {
		sIndexList
	},
	onLoad() {
		this.getListData()
	},
	methods: {
		getListData() {
			this.request({
				url: "https://m2.qiushibaike.com/article/newlist?new=1&readarticles=[123018375]"
			}).then(result => {
				this.listData = result.items
			})
		}
	}
}
